body {
  font-family: $main-font;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

li {
  list-style: none;
}

ul,
ol,
li,
a {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
}

.link {
  text-decoration: none;
}

.list {
  list-style-type: none;
}

.address {
  font-style: normal;
}

